.product-variants-sticky {
  background-color: #fff;
  bottom: 0;
  box-shadow: 2.72076px -8px 24px rgb(0 0 0 / 0.15);
  box-sizing: border-box;
  height: auto;
  left: 0;
  padding: 0.6rem;
  position: fixed;
  margin: 0;
  transition: bottom 0.5s ease-in-out;
  width: 100%;
  z-index: 2;
}
